import SearchIcon from "@mui/icons-material/Search";
import InputBase from "@mui/material/InputBase";
import { styled } from "@mui/material/styles";
import { createTheme } from "@mui/material/styles";
import useDebounce from "../../../../hooks/useDebounce";
import useApi from "../../../../hooks/useApi";
import { useState } from "react";
import { useEffect } from "react";
import { useNavigate } from "react-router-dom";

const theme = createTheme({
  breakpoints: {
    values: {
      sm: 640,
      md: 768,
      lg: 1024,
    },
  },
});

const StyledSearchIcon = styled(SearchIcon)(() => ({
  alignSelf: "center",
  left: "0.2ch",
  position: "absolute",
}));
const StyledInputBase = styled(InputBase)(({ theme }) => ({
  width: "100%",
  "& .MuiInputBase-input": {
    padding: theme.spacing(0.5, 0, 0.5, 0),
    paddingLeft: `calc(.2em + ${theme.spacing(3)})`,
    transition: theme.transitions.create("width"),
    [theme.breakpoints.down("sm")]: {
      width: "0ch",
      "&:focus": {
        width: "8rem",
      },
    },
    [theme.breakpoints.up("sm")]: {
      width: "12rem",
      "&:focus": {
        width: "12rem",
      },
    },
    [theme.breakpoints.up("md")]: {
      width: "14rem",
      "&:focus": {
        width: "14rem",
      },
    },
  },
}));

export default function SearchBar() {
  const { data, get } = useApi();
  const [searchTerm, setSearchTerm] = useState("");
  const [suggestions, setSuggestions] = useState([]);
  const navigate = useNavigate();

  const handleSearch = useDebounce((term) => {
    get(`/products?search=${term}`);
  }, 100);

  const handleChange = (event) => {
    setSearchTerm(event.target.value);

    if (event.target.value.length > 0) {
      handleSearch(event.target.value);
    } else {
      setSuggestions([]);
    }
  };

  const onSuggestionClick = (productId) => {
    setSuggestions([]);
    setSearchTerm("");
    navigate(`/product/${productId}`);
  };

  useEffect(() => {
    if (data?.products) {
      setSuggestions(data.products);
    }
  }, [data]);

  const handleEnterPress = (e) => {
    if (e.key === "Enter") {
      setSuggestions([]);
      setSearchTerm("");
      navigate("/search", { state: { products: suggestions } });
    }
  };

  return (
    <div className="relative flex mr-2 p-1 rounded-lg sm:bg-color-grey">
      <StyledSearchIcon className="text-color-primary" />
      <StyledInputBase
        theme={theme}
        onBlur={() => {
          setSuggestions([]);
        }}
        placeholder="Search for products or brands....."
        inputProps={{ "aria-label": "search" }}
        sx={{
          fontWeight: 500,
          fontSize: "0.875rem",
        }}
        value={searchTerm}
        onChange={handleChange}
        onKeyDown={(e) => {
          handleEnterPress(e);
        }}
      />
      <ul className="absolute z-10 w-full right-0 top-10 rounded-lg overflow-hidden">
        {suggestions?.slice(0, 4).map((suggestion) => (
          <li
            onClick={() => onSuggestionClick(suggestion.id)}
            className="bg-color-accent hover:cursor-pointer p-3 shadow-md border-b border-color-lightText hover:bg-color-grey"
            key={suggestion.id}
          >
            <div className="flex items-center ">
              <img
                className="w-[3.5rem] max-h-[3.3rem] rounded-md mr-2"
                src={suggestion.image_secure_url}
                alt=""
              />
              <div className="flex flex-col text-color-typeHighEmphasis">
                <span className="text-sm">{suggestion.name}</span>
                <span className="text-xs">{suggestion.category}</span>
              </div>
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
}
